/* eslint-disable react-hooks/exhaustive-deps */
import classNames from "classnames";
import { useFormik } from "formik";
import { FC, KeyboardEvent, useCallback, useMemo, useState } from "react";
import { message } from 'antd';
import * as Yup from 'yup';
import Web3 from 'web3';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { useWeb3React } from '@web3-react/core';
import { Link } from "react-router-dom";
import { parseUnits, parseEther } from '@ethersproject/units';
import { useEffect } from "react";

import Button from "../common/Button";
import configs from "../../configs";
import getPath, { RouteNames } from "../../utils/path";
import useSupportedRouter from "../../hooks/useSupportedRouter";
import useTokenRouter from "../../hooks/useTokenRouter";
import useExplorerDomain from "../../hooks/useExplorerDomain";
import { useChainId, useWeb3 } from "../../packages/web3/hooks";
import { getUniswapFactoryContract, getUniswapV2RouterContract, useBabyTokenContract, useLiquidityTokenContract, useStandardTokenContract } from "../../hooks/useContract";
import useTokenFee from "../../hooks/useTokenFee";
import multicall from "../../utils/multicall";
import { currencyFormat } from "../../utils/helper";

const Erc20Abi = require('../../abi/ERC20Abi.json');

type TForm = {
  tokenName: string;
  tokenSymbol: string;
  tokenDecimals?: number;
  totalSupply?: string;
  // liquidity token
  charity?: string;
  charityBps?: number;
  taxFeeBps?: number;
  liquidityFeeBps?: number;
  tokenType?: string;
  router?: string;
  maxTxBps?: number;
  // baby token has router,
  rewardToken?: string;
  marketingWallet?: string;
  tokenRewardsFee?: number;
  liquidityFee?: number;
  marketingFee?: number;
  minimumTokenBalanceForDividends?: number;
};


const validationSchema: Yup.SchemaOf<TForm> = Yup.object().shape({
  tokenName: Yup.string()
    .required()
    .min(2)
    .max(255),
  tokenSymbol: Yup.string()
    .required()
    .min(2)
    .max(255),
  tokenDecimals: Yup.number()
    .positive()
    .min(2)
    .max(64)
    .test({
      test: (value: any, ctx: any) => {
        if (ctx.parent.tokenType === 'standard') {
          return value && value > 0;
        };
        return true;;
      },
      message: 'Invalid decimals'
    }),
  totalSupply: Yup.string().required(),
  taxFeeBps: Yup.number()
    .positive()
    .max(100)
    .min(0.01),
  liquidityFeeBps: Yup.number()
    .positive()
    .max(100)
    .min(0.01),
  tokenType: Yup.string(),
  router: Yup.string().test({
    test: (value: any, ctx: any) => {
      if (ctx.parent.tokenType === 'standard') return true;
      return value && value !== '';
    },
    message: 'Invalid router'
  }),
  maxTxBps: Yup.number()
    .positive()
    .max(100)
    .min(0.01)
    .test({
      test: (value: any, ctx: any) => {
        if (ctx.parent.tokenType === 'standard' || ctx.parent.tokenType === 'baby') return true;
        return value && value >= 0.01;
      },
      message: 'Invalid max tx percent'
    }),
  charity: Yup.string()
    .max(255)
    .test({
      test: (value: any, ctx: any) => {
        if (ctx.parent.tokenType === 'standard') return true;
        if (!value) return true;
        return Web3.utils.isAddress(value) && value !== configs.ZERO_ADDRESS;
      },
      message: 'Address is invalid'
    }),
  charityBps: Yup.number()
    .positive()
    .max(100)
    .min(1)
    .when('charity', (c: any) => {
      if (c) {
        return Yup.number()
          .positive()
          .max(100)
          .min(1)
          .required();
      }
      return Yup.number()
        .positive()
        .max(100)
        .min(1);
    }),
  rewardToken: Yup.string()
    .test({
      test: (value: any, ctx: any) => {
        if (ctx.parent.tokenType === 'standard' || ctx.parent.tokenType === 'liquidity') return true;
        if (!value) return true;
        return Web3.utils.isAddress(value) && value !== configs.ZERO_ADDRESS;
      },
      message: 'Address is invalid'
    }),
  tokenRewardsFee: Yup.number()
    .positive()
    .max(100)
    .min(0.01)
    .when('tokenType', (c: any) => {
      if (c === 'baby') {
        return Yup.number()
          .positive()
          .max(100)
          .min(0.01)
          .required();
      }
      return Yup.number()
        .positive()
        .max(100)
        .min(0.01);
    }),
  liquidityFee: Yup.number()
    .positive()
    .max(100)
    .min(0.01)
    .when('tokenType', (c: any) => {
      if (c === 'baby') {
        return Yup.number()
          .positive()
          .max(100)
          .min(0.01)
          .required();
      }
      return Yup.number()
        .positive()
        .max(100)
        .min(0.01);
    }),
  marketingWallet: Yup.string()
    .test({
      test: (value: any, ctx: any) => {
        if (ctx.parent.tokenType === 'standard' || ctx.parent.tokenType === 'liquidity') return true;
        if (!value) return true;
        return Web3.utils.isAddress(value) && value !== configs.ZERO_ADDRESS;
      },
      message: 'Address is invalid'
    }),
  marketingFee: Yup.number()
    .positive()
    .max(100)
    .min(0.01)
    .when('tokenType', (c: any) => {
      if (c === 'baby') {
        return Yup.number()
          .positive()
          .max(100)
          .min(0.01)
          .required();
      }
      return Yup.number()
        .positive()
        .max(100)
        .min(0.01);
    }),
  minimumTokenBalanceForDividends: Yup.number()
    .positive()
    .min(1)
    .when('tokenType', (c: any) => {
      if (c === 'baby') {
        return Yup.number()
          .positive()
          .min(1)
          .required();
      }
      return Yup.number()
        .positive()
        .min(1);
    }),
});

// createStandardToken
// function create(
//   string memory name, 
//   string memory symbol, 
//   uint8 decimals, 
//   uint256 totalSupply
// )

// LiquidityGeneratorFactory
// create(
//   string memory name,
//   string memory symbol,
//   uint256 totalSupply,
//   address router,
//   uint16 taxFeeBps, 
//   uint16 liquidityFeeBps,
//   uint16 maxTxBps,
// ) 

// baby token
// function create(
//   string memory name_,
//   string memory symbol_,
//   uint256 totalSupply_,
//   address rewardToken_,
//   address router_,
//   address marketingWallet_,
//   uint256 tokenRewardsFee_,
//   uint256 liquidityFee_,
//   uint256 marketingFee_,
//   uint256 minimumTokenBalanceForDividends_
// )

type Props = {
  onSuccess?(address?: string): void;
};

const CreateTokenForm: FC<Props> = ({ onSuccess }) => {
  const [loading, setLoading] = useState<boolean>(false);
  const [isSuccess, setIsSuccess] = useState<boolean>(false);
  const [rewardTokenInfo, setRewardTokenInfo] = useState<{
    name: string;
    symbol: string;
    decimals: string;
  }>();
  const [txId, setTxId] = useState<string>();
  const [tokenId, setTokenId] = useState<string>();
  const [checkPairError, setCheckPairError] = useState<string>();
  const web3 = useWeb3();
  const { account } = useWeb3React();
  const chainId = useChainId();
  const { routers } = useSupportedRouter();
  const tokenRouters = useTokenRouter()
  const explorerDomain = useExplorerDomain();
  const standardTokenContract = useStandardTokenContract(web3);
  const liquidityGeneratorTokenContract = useLiquidityTokenContract(web3);
  const babyTokenContract = useBabyTokenContract(web3);
  const standardFee = useTokenFee('standard');
  const liquidityFee = useTokenFee('liquidity');
  const babyFee = useTokenFee('baby');
  const createStandardToken = useCallback((name: string, symbol: string, decimals: number, totalSupply: string) => {
    (async () => {
      if (!standardTokenContract || !account || !standardFee) {
        message.error('Connect web3 to continue.');
        return;
      }
      setLoading(true);
      try {
        const tx = await standardTokenContract.methods.create(
          name,
          symbol,
          decimals,
          parseUnits(totalSupply, Number(decimals)).toString()
        ).send({
          from: account,
          value: standardFee,
        });
        message.success('Create token success');
        setIsSuccess(true);
        setTxId(tx.transactionHash);
        getReceipt(standardTokenContract, tx.transactionHash, tx.blockNumber);
      } catch (e) {
        if (e && e.message) {
          message.error(e.message);
        }
      } finally {
        setLoading(false);
      }
    })();
  }, [standardTokenContract, account, standardFee]);
  const createLiquidityGeneratorToken = useCallback((
    name: string,
    symbol: string,
    totalSupply: string,
    router: string,
    taxFeeBps: number,
    liquidityFeeBps: number,
    maxTxBps: number,
    charity: string,
    charityBps: number,
  ) => {
    (async () => {
      if (!liquidityGeneratorTokenContract || !account || !liquidityFee) {
        message.error('Connect web3 to continue.');
        return;
      }
      setLoading(true);
      try {
        const tx = await liquidityGeneratorTokenContract.methods.create(
          name,
          symbol,
          parseUnits(totalSupply, 9).toString(),
          router,
          charity,
          taxFeeBps,
          liquidityFeeBps,
          charityBps,
          maxTxBps,
        ).send({
          from: account,
          value: liquidityFee,
        });
        message.success('Create token success');
        setIsSuccess(true);
        setTxId(tx.transactionHash);
        getReceipt(liquidityGeneratorTokenContract, tx.transactionHash, tx.blockNumber);
      } catch (e) {
        if (e && e.message) {
          message.error(e.message);
        }
      } finally {
        setLoading(false);
      }
    })();
  }, [liquidityGeneratorTokenContract, account, liquidityFee]);
  const createBabyToken = useCallback((
    name: string,
    symbol: string,
    totalSupply: string,
    rewardToken: string,
    router: string,
    marketingWallet: string,
    tokenRewardsFee: number,
    liquidityFee: number,
    marketingFee: number,
    minimumTokenBalanceForDividends: number,
  ) => {
    (async () => {
      if (!babyTokenContract || !account || !babyFee) {
        message.error('Connect web3 to continue.');
        return;
      }
      setLoading(true);
      try {
        const tx = await babyTokenContract.methods.create(
          name,
          symbol,
          parseEther(totalSupply).toString(),
          rewardToken,
          router,
          marketingWallet,
          tokenRewardsFee,
          liquidityFee,
          marketingFee,
          parseEther(`${minimumTokenBalanceForDividends || 0}`).toString(),
        ).send({
          from: account,
          value: babyFee,
        });
        message.success('Create token success');
        setIsSuccess(true);
        setTxId(tx.transactionHash);
        getReceipt(babyTokenContract, tx.transactionHash, tx.blockNumber);
      } catch (e) {
        if (e && e.message) {
          message.error(e.message);
        }
      } finally {
        setLoading(false);
      }
    })();
  }, [babyTokenContract, account, babyFee]);
  const formik = useFormik<TForm>({
    initialValues: {
      tokenName: '',
      tokenSymbol: '',
      router: '',
      tokenType: 'standard',
    },
    validateOnMount: true,
    validationSchema,
    onSubmit: (values: TForm) => {
      if (values.tokenType === 'standard') {
        createStandardToken(
          values.tokenName,
          values.tokenSymbol,
          values.tokenDecimals || 18,
          values.totalSupply || '0',
        );
      } else if (values.tokenType === 'liquidity') {
        createLiquidityGeneratorToken(
          values.tokenName,
          values.tokenSymbol,
          values.totalSupply || '0',
          values.router || '',
          Math.floor((Number(values.taxFeeBps) || 0) * 100),
          Math.floor(Number(values.liquidityFeeBps || 0) * 100),
          Math.floor(Number(values.maxTxBps || 0) * 100),
          values.charity || configs.ZERO_ADDRESS,
          Math.floor(Number(values.charityBps || 0) * 100),
        );
      } else {
        createBabyToken(
          values.tokenName,
          values.tokenSymbol,
          values.totalSupply || '0',
          values.rewardToken || '',
          values.router || '',
          values.marketingWallet || '',
          Number(values.tokenRewardsFee || 0),
          Number(values.liquidityFee || 0),
          Number(values.marketingFee || 0),
          Number(values.minimumTokenBalanceForDividends || 0),
        );
      }
    },
  });
  const getReceipt = (contract: any, txHash: string, blockNumber: number) => {
    if (!contract) return;
    (async () => {
      try {
        await contract.events.TokenCreated({
          fromBlock: blockNumber - 1,
        }).on('data', ({ returnValues, transactionHash }: any) => {
          // returnValues
          // owner: "0x..."
          // token: "0x..."
          // tokenType: "0" | 1
          if (transactionHash === txHash) {
            setTokenId(returnValues.token);
            onSuccess?.(returnValues.token);
          }
        });
      } catch (e) {
        //
      }
    })();
  };
  const isTaxValid = useMemo(() => {
    if (formik.values.charityBps) {
      return (
        Number(formik.values.maxTxBps || 0) + Number(formik.values.taxFeeBps || 0) + Number(formik.values.charityBps)
      ) <= 10000;
    }
    return true;
  }, [formik.values]);
  const onlyInputAlphabet = (e: KeyboardEvent<HTMLInputElement>) => {
    if (!/[a-z0-9]/i.test(e.key)) {
      e.preventDefault();
    }
  }
  const onlyInputNumber = (e: KeyboardEvent<HTMLInputElement>) => {
    if (!/[0-9]/i.test(e.key)) {
      e.preventDefault();
    }
  }
  const disablePaste = (e: any) => {
    e.preventDefault();
  }

  useEffect(() => {
    if (!formik.values.marketingWallet || !account) return;
    if (formik.values.marketingWallet === account) {
      formik.setFieldError('marketingWallet', 'Owner and marketing wallet cannot be the same');
    }
  }, [formik.values.marketingWallet, account]);

  useEffect(() => {
    if (!formik.values.rewardToken || !Web3.utils.isAddress(formik.values.rewardToken)) return;
    (async () => {
      try {
        const calls = ["name", "symbol", "decimals"].map(
          (method) => ({
            address: formik.values.rewardToken || '',
            name: method,
          })
        );
        const [[name], [symbol], [decimals]] = await multicall(
          Erc20Abi,
          calls,
          {
            web3,
          },
          chainId
        );
        setRewardTokenInfo({
          name,
          symbol,
          decimals: decimals.toString(),
        });
      } catch (e) {
        setRewardTokenInfo(undefined);
        formik.setFieldError('rewardToken', 'Cannot get token info');
      }
    })();
  }, [formik.values.rewardToken, web3, chainId]);


  useEffect(() => {
    if (!formik.values.rewardToken || !formik.values.router || !Web3.utils.isAddress(formik.values.rewardToken)) return;
    (async () => {
      try {
        const routerContract = getUniswapV2RouterContract(formik.values.router, web3);
        const nativeTokenAddress = await routerContract.methods.WETH().call();
        const factoryAddress = await routerContract.methods.factory().call();
        const factoryContract = getUniswapFactoryContract(factoryAddress, web3);
        const pair = await factoryContract.methods.getPair(formik.values.rewardToken, nativeTokenAddress).call();
        if (pair !== configs.ZERO_ADDRESS) {
          setCheckPairError(undefined);
        } else {
          setCheckPairError('Reward token invalid');
        };
      } catch (e) {
        console.log(e);
      }
    })();
  }, [formik.values.rewardToken, web3, chainId, formik.values.router]);

  if (isSuccess) {
    return (
      <div>
        <div className="has-text-centered has-text-success is-size-5 my-4">
          Your token was created!
        </div>
        <div className="table-container">
          <table className="table is-bordered is-fullwidth">
            <tbody>
              <tr>
                <td>Name</td>
                <td className="has-text-primary">{formik.values.tokenName}</td>
              </tr>
              <tr>
                <td>Symbol</td>
                <td className="has-text-primary">{formik.values.tokenSymbol}</td>
              </tr>
              <tr>
                <td>Total supply</td>
                <td className="has-text-primary">{currencyFormat(formik.values.totalSupply || '0')}</td>
              </tr>
              {tokenId ? (
                <tr>
                  <td>Address</td>
                  <td className="has-text-primary">{tokenId}</td>
                </tr>
              ) : null}
              {formik.values.taxFeeBps ? (
                <tr>
                  <td>taxFee percent</td>
                  <td>{Number(formik.values.taxFeeBps || 0)}%</td>
                </tr>
              ) : null}
              {formik.values.liquidityFeeBps ? (
                <tr>
                  <td>liquidityFee percent</td>
                  <td>{Number(formik.values.liquidityFeeBps || 0)}%</td>
                </tr>
              ) : null}
              {formik.values.maxTxBps ? (
                <tr>
                  <td>maxTx percent</td>
                  <td>{Number(formik.values.maxTxBps || 0)}%</td>
                </tr>
              ) : null}
            </tbody>
          </table>
        </div>
        <div className="has-text-centered mb-4 is-flex is-align-items-center is-flex-wrap-wrap">
          <a className="button m-4 is-flex-grow-1" href={`${explorerDomain}/tx/${txId}`} target="_blank" rel="nofollow noreferrer">View transaction on BSCScan</a>
          {tokenId ? (
            <CopyToClipboard text={tokenId} onCopy={() => message.success('Copied!')}>
              <a className="button m-4 is-flex-grow-1">Copy address</a>
            </CopyToClipboard>
          ) : null}
          <Link className="button is-primary m-4 is-flex-grow-1" to={`${getPath(RouteNames.CreateLauchpad)}?token=${tokenId}`}>Create launchpad</Link>
        </div>
      </div>
    );
  }
  return (
    <form onSubmit={formik.handleSubmit}>
      <p className="has-text-primary is-size-7">(*) is required field.</p>
      <div className="field">
        <label htmlFor="tokenType" className="label">Token Type<sup className="has-text-danger">*</sup></label>
        <div className="control">
          <select
            name="tokenType"
            id="tokenType"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            value={formik.values.tokenType}
            className="input"
          >
            <option value="standard">Standard Token</option>
            <option value="liquidity">Liquidity Generator Token</option>
            <option value="baby">Baby Token</option>
          </select>
          {Boolean(formik.touched.tokenType && formik.errors.tokenType) && (
            <p className="help is-danger">{formik.errors.tokenType}</p>
          )}
        </div>
      </div>

      <div className="field">
        <label htmlFor="tokenName" className="label">Name<sup className="has-text-danger">*</sup></label>
        <div className="control">
          <input
            type="text"
            className={classNames('input', formik.touched.tokenName && formik.errors.tokenName ? 'is-danger' : '')}
            id="tokenName"
            name="tokenName"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            placeholder="Ex: Ethereum"
            maxLength={255}
            onKeyPress={onlyInputAlphabet}
          />
          {Boolean(formik.touched.tokenName && formik.errors.tokenName) && (
            <p className="help is-danger">{formik.errors.tokenName}</p>
          )}
        </div>
      </div>

      <div className="field">
        <label htmlFor="tokenSymbol" className="label">Symbol<sup className="has-text-danger">*</sup></label>
        <div className="control">
          <input
            type="text"
            className={classNames('input', formik.touched.tokenSymbol && formik.errors.tokenSymbol ? 'is-danger' : '')}
            id="tokenSymbol"
            name="tokenSymbol"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            placeholder="Ex: ETH"
            maxLength={255}
            onKeyPress={onlyInputAlphabet}
            onPaste={disablePaste}
          />
          {Boolean(formik.touched.tokenSymbol && formik.errors.tokenSymbol) && (
            <p className="help is-danger">{formik.errors.tokenSymbol}</p>
          )}
        </div>
      </div>

      {formik.values.tokenType === 'standard' && (
        <div className="field">
          <label htmlFor="tokenDecimals" className="label">Decimals<sup className="has-text-danger">*</sup></label>
          <div className="control">
            <input
              type="number"
              className={classNames('input', formik.touched.tokenDecimals && formik.errors.tokenDecimals ? 'is-danger' : '')}
              id="tokenDecimals"
              name="tokenDecimals"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              placeholder="Ex: 18"
              onKeyPress={onlyInputNumber}
              onPaste={disablePaste}
            />
            {Boolean(formik.touched.tokenDecimals && formik.errors.tokenDecimals) && (
              <p className="help is-danger">{formik.errors.tokenDecimals}</p>
            )}
          </div>
        </div>
      )}

      <div className="field">
        <label htmlFor="totalSupply" className="label">Total supply<sup className="has-text-danger">*</sup></label>
        <div className="control">
          <input
            type="text"
            className={classNames('input', formik.touched.totalSupply && formik.errors.totalSupply ? 'is-danger' : '')}
            id="totalSupply"
            name="totalSupply"
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            placeholder="Ex: 100000000000"
            onKeyPress={onlyInputNumber}
            onPaste={disablePaste}
          />
          {Boolean(formik.touched.totalSupply && formik.errors.totalSupply) && (
            <p className="help is-danger">{formik.errors.totalSupply}</p>
          )}
        </div>
      </div>

      {formik.values.tokenType === 'liquidity' && (
        <div className="field">
          <div className="control">
            <label htmlFor="router" className="label">Router</label>
            <select
              name="router"
              id="router"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.router}
              className="input"
            >
              <option value="" disabled>---Select router---</option>
              {routers?.map((r, i) => (
                <option key={r} value={r}>{configs.ROUTER_NAMES[`${chainId}`][i] ?? r}</option>
              ))}
            </select>
            {Boolean(formik.touched.router && formik.errors.router) && (
              <p className="help is-danger">{formik.errors.router}</p>
            )}
          </div>
        </div>
      )}

      {formik.values.tokenType === 'baby' && (
        <div className="field">
          <div className="control">
            <label htmlFor="router" className="label">Router</label>
            <select
              name="router"
              id="router"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.router}
              className="input"
            >
              <option value="" disabled>---Select router---</option>
              {tokenRouters.map(({ name, address }) => (
                <option key={address} value={address}>{name}</option>
              ))}
            </select>
            {Boolean(formik.touched.router && formik.errors.router) && (
              <p className="help is-danger">{formik.errors.router}</p>
            )}
          </div>
        </div>
      )}

      {formik.values.tokenType === 'baby' && (
        <>
          <div className="columns">
            <div className="column">
              <div className="field">
                <label htmlFor="rewardToken" className="label">Reward token<sup className="has-text-danger">*</sup></label>
                <div className="control">
                  <input
                    type="text"
                    className={classNames('input', (formik.touched.rewardToken && formik.errors.rewardToken) || checkPairError ? 'is-danger' : '')}
                    id="rewardToken"
                    name="rewardToken"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    placeholder="Ex: 0x...."
                    maxLength={255}
                    onKeyPress={onlyInputAlphabet}
                    value={formik.values.rewardToken}
                  />
                  {checkPairError ? (
                    <p className="help is-danger">{checkPairError}</p>
                  ) : (
                    <>
                      {Boolean(formik.touched.rewardToken && formik.errors.rewardToken) && (
                        <p className="help is-danger">{formik.errors.rewardToken}</p>
                      )}
                    </>
                  )}
                  {rewardTokenInfo ? (
                    <div className="mt-2 is-size-7">
                      <div className="is-flex is-align-items-center">
                        <div className="is-flex-grow-1">Name</div>
                        <div className="has-text-info">{rewardTokenInfo.name}</div>
                      </div>
                      <div className="is-flex is-align-items-center">
                        <div className="is-flex-grow-1">Symbol</div>
                        <div className="has-text-info">{rewardTokenInfo.symbol}</div>
                      </div>
                      <div className="is-flex is-align-items-center">
                        <div className="is-flex-grow-1">Decimals</div>
                        <div className="has-text-info">{rewardTokenInfo.decimals}</div>
                      </div>
                    </div>
                  ) : null}
                </div>
              </div>
            </div>
            <div className="column">
              <div className="field">
                <label htmlFor="marketingWallet" className="label">Maketing wallet<sup className="has-text-danger">*</sup></label>
                <div className="control">
                  <input
                    type="text"
                    className={classNames('input', formik.errors.marketingWallet || formik.values.marketingWallet === account ? 'is-danger' : '')}
                    id="marketingWallet"
                    name="marketingWallet"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    placeholder="Ex: 0x...."
                    maxLength={255}
                    onKeyPress={onlyInputAlphabet}
                    value={formik.values.marketingWallet}
                  />
                  {formik.values.marketingWallet === account ? (
                    <p className="help is-danger">Owner and marketing wallet cannot be the same</p>
                  ) : (
                    <>
                      {formik.errors.marketingWallet && (
                        <p className="help is-danger">{formik.errors.marketingWallet}</p>
                      )}
                    </>
                  )}
                </div>
              </div>
            </div>
          </div>

          <div className="columns">
            <div className="column">
              <div className="field">
                <label htmlFor="tokenRewardsFee" className="label">Token reward fee (%)<sup className="has-text-danger">*</sup></label>
                <div className="control">
                  <input
                    className={classNames('input', formik.touched.tokenRewardsFee && formik.errors.tokenRewardsFee ? 'is-danger' : '')}
                    type="number"
                    id="tokenRewardsFee"
                    name="tokenRewardsFee"
                    min="0"
                    max="100"
                    step="0.01"
                    placeholder="0 - 100"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.tokenRewardsFee || undefined}
                  />
                  {Boolean(formik.touched.tokenRewardsFee && formik.errors.tokenRewardsFee) && (
                    <p className="help is-danger">{formik.errors.tokenRewardsFee}</p>
                  )}
                </div>
              </div>
            </div>
            <div className="column">
              <div className="field">
                <label htmlFor="liquidityFee" className="label">Liquidity fee (%)<sup className="has-text-danger">*</sup></label>
                <div className="control">
                  <input
                    className={classNames('input', formik.touched.liquidityFee && formik.errors.liquidityFee ? 'is-danger' : '')}
                    type="number"
                    id="liquidityFee"
                    name="liquidityFee"
                    min="0"
                    max="100"
                    step="0.01"
                    placeholder="0 - 100"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.liquidityFee || undefined}
                  />
                  {Boolean(formik.touched.liquidityFee && formik.errors.liquidityFee) && (
                    <p className="help is-danger">{formik.errors.liquidityFee}</p>
                  )}
                </div>
              </div>
            </div>
          </div>

          <div className="columns">
            <div className="column">
              <div className="field">
                <label htmlFor="marketingFee" className="label">Marketing fee (%)<sup className="has-text-danger">*</sup></label>
                <div className="control">
                  <input
                    className={classNames('input', formik.touched.marketingFee && formik.errors.marketingFee ? 'is-danger' : '')}
                    type="number"
                    id="marketingFee"
                    name="marketingFee"
                    min="0"
                    max="100"
                    step="0.01"
                    placeholder="0 - 100"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    value={formik.values.marketingFee || undefined}
                  />
                  {Boolean(formik.touched.marketingFee && formik.errors.marketingFee) && (
                    <p className="help is-danger">{formik.errors.marketingFee}</p>
                  )}
                </div>
              </div>
            </div>
            <div className="column">
              <div className="field">
                <label htmlFor="minimumTokenBalanceForDividends" className="label">Minimum token balance for dividends<sup className="has-text-danger">*</sup></label>
                <div className="control">
                  <input
                    type="text"
                    className={classNames('input', formik.touched.minimumTokenBalanceForDividends && formik.errors.minimumTokenBalanceForDividends ? 'is-danger' : '')}
                    id="minimumTokenBalanceForDividends"
                    name="minimumTokenBalanceForDividends"
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    placeholder="Ex: 100000000000"
                    onKeyPress={onlyInputNumber}
                    onPaste={disablePaste}
                    value={formik.values.minimumTokenBalanceForDividends}
                  />
                  {Boolean(formik.touched.minimumTokenBalanceForDividends && formik.errors.minimumTokenBalanceForDividends) && (
                    <p className="help is-danger">{formik.errors.minimumTokenBalanceForDividends}</p>
                  )}
                </div>
              </div>
            </div>
          </div>
        </>
      )}

      {formik.values.tokenType === 'liquidity' && (
        <div className="columns">
          <div className="column">
            <div className="field">
              <label htmlFor="taxFeeBps" className="label">Transaction fee to generate yield (%)</label>
              <div className="control">
                <input
                  className={classNames('input', formik.touched.taxFeeBps && formik.errors.taxFeeBps ? 'is-danger' : '')}
                  type="number"
                  id="taxFeeBps"
                  name="taxFeeBps"
                  min="0"
                  max="100"
                  step="0.01"
                  placeholder="0 - 100"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.taxFeeBps || undefined}
                />
                {Boolean(formik.touched.taxFeeBps && formik.errors.taxFeeBps) && (
                  <p className="help is-danger">{formik.errors.taxFeeBps}</p>
                )}
              </div>
            </div>
          </div>
          <div className="column">
            <div className="field">
              <label htmlFor="liquidityFeeBps" className="label">Transaction fee to generate liquidity (%)</label>
              <div className="control">
                <input
                  className={classNames('input', formik.touched.liquidityFeeBps && formik.errors.liquidityFeeBps ? 'is-danger' : '')}
                  type="number"
                  id="liquidityFeeBps"
                  name="liquidityFeeBps"
                  min="0"
                  max="100"
                  step="0.01"
                  placeholder="0 - 100"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.liquidityFeeBps || undefined}
                />
                {Boolean(formik.touched.liquidityFeeBps && formik.errors.liquidityFeeBps) && (
                  <p className="help is-danger">{formik.errors.liquidityFeeBps}</p>
                )}
              </div>
            </div>
          </div>
        </div>
      )}

      {formik.values.tokenType === 'liquidity' && (
        <div className="columns">
          <div className="column">
            <div className="field">
              <label htmlFor="maxTxBps" className="label">Max transaction percent (%)<sup className="has-text-danger">*</sup></label>
              <div className="control">
                <input
                  className={classNames('input', formik.touched.maxTxBps && formik.errors.maxTxBps ? 'is-danger' : '')}
                  type="number"
                  id="maxTxBps"
                  name="maxTxBps"
                  min="0"
                  max="100"
                  step="0.01"
                  placeholder="0 - 100"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.maxTxBps || undefined}
                />
                {Boolean(formik.touched.maxTxBps && formik.errors.maxTxBps) && (
                  <p className="help is-danger">{formik.errors.maxTxBps}</p>
                )}
              </div>
            </div>
          </div>
        </div>
      )}

      {formik.values.tokenType === 'liquidity' && (
        <div className="field">
          <label htmlFor="charity" className="label">Charity address</label>
          <div className="control">
            <input
              type="text"
              className={classNames('input', formik.touched.charity && formik.errors.charity ? 'is-danger' : '')}
              id="charity"
              name="charity"
              value={formik.values.charity}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              placeholder="Ex: 0x....."
            />
            {Boolean(formik.touched.charity && formik.errors.charity) && (
              <p className="help is-danger">{formik.errors.charity}</p>
            )}
          </div>
        </div>
      )}

      {formik.values.tokenType === 'liquidity' && (
        <div className="columns">
          <div className="column">
            <div className="field">
              <label htmlFor="charityBps" className="label">Charity percent (%)</label>
              <div className="control">
                <input
                  className={classNames('input', formik.touched.charityBps && formik.errors.charityBps ? 'is-danger' : '')}
                  type="number"
                  id="charityBps"
                  name="charityBps"
                  min="0"
                  max="100"
                  step="0.01"
                  placeholder="0 - 100"
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  value={formik.values.charityBps || undefined}
                />
                {Boolean(formik.touched.charityBps && formik.errors.charityBps) && (
                  <p className="help is-danger">{formik.errors.charityBps}</p>
                )}
              </div>
            </div>
          </div>
        </div>
      )}

      <div className="has-text-centered mt-6 mb-4">
        {!isTaxValid && <div className="px-2 has-text-danger">taxFee percent + liquidityFee percent + charity percent must be less than or equal 100%</div>}
        <Button primary type="submit" loading={loading} disabled={!formik.isValid || !!checkPairError}>Create token</Button>
      </div>

    </form>
  );
};

export default CreateTokenForm;
