import { useWeb3React } from "@web3-react/core";
import { message, Modal } from "antd";
import { useState, memo } from "react";
import { useCallback } from "react";
import { FC } from "react";
import { usePoolV2Contract } from "../../hooks/useContract";
import { useWeb3 } from "../../packages/web3/hooks";

import Button from "../common/Button";
import WhitelistForm from "./WhitelistForm";

type TModal = {
  add: boolean;
  remove: boolean;
};

type Props = {
  address: string;
  useWhitelisting?: boolean;
  version?: number;
};

const WhitelistAction: FC<Props> = ({ address, useWhitelisting, version }) => {
  const [loading, setLoading] = useState<boolean>(false);
  const { account } = useWeb3React();
  const web3 = useWeb3();
  const contract = usePoolV2Contract(address, web3);
  const [modal, setModal] = useState<TModal>({
    add: false,
    remove: false,
  });

  const changeWhitelistStatus = useCallback((status: boolean) => {
    if (!contract || !account) return;
    (async () => {
      try {
        setLoading(true);
        await contract.methods.setUseWhitelisting(status).send({
          from: account,
        });
        message.success("Whitelist status changed");
      } catch (e) {
        message.error(e.message || e.toString());
      } finally {
        setLoading(false);
      }
    })();
  }, [contract, account]);
  const enableWhitelist = () => changeWhitelistStatus(true);
  const disableWhitelist = () => changeWhitelistStatus(false);
  const openAddModal = () => setModal((prev) => ({ ...prev, add: true }));
  const closeAddModal = () => setModal((prev) => ({ ...prev, add: false }));
  const openRemoveModal = () => setModal((prev) => ({ ...prev, remove: true }));
  const closeRemoveModal = () => setModal((prev) => ({ ...prev, remove: false }));
  return (
    <>
      {useWhitelisting ? (
        <>
          <div className="my-2">
            <Button fullwidth onClick={openAddModal}>Add users to whitelist</Button>
          </div>
          <div className="my-2">
            <Button fullwidth onClick={openRemoveModal}>Remove users from whitelist</Button>
          </div>
          <div className="my-2">
            <Button fullwidth onClick={disableWhitelist} loading={loading}>Disable whitelist</Button>
          </div>
        </>
      ) : (
        <>
          {version === 3 ? (
            <div className="my-2">
              <Button fullwidth onClick={enableWhitelist} loading={loading}>Enable whitelist</Button>
            </div>
          ) : null}
        </>
      )}
      <Modal
        title="Add users to whitelist"
        footer={null}
        visible={modal.add}
        destroyOnClose
        onCancel={closeAddModal}
        maskClosable={false}
      >
        <WhitelistForm
          address={address}
        />
      </Modal>
      <Modal
        title="Remove users from whitelist"
        footer={null}
        visible={modal.remove}
        destroyOnClose
        onCancel={closeRemoveModal}
        maskClosable={false}
      >
        <WhitelistForm
          address={address}
          type="Remove"
        />
      </Modal>
    </>
  )
};

export default memo(WhitelistAction);
